import { useState, useEffect } from "react";
import { Container, Row, Col } from "react-bootstrap";
import headerImg from "../assets/img/header-img.gif";
import { ArrowRightCircle } from "react-bootstrap-icons";
import "animate.css";
import { Helmet } from "react-helmet";
import TrackVisibility from "react-on-screen";

export const Banner = () => {
  const [loopNum, setLoopNum] = useState(0);
  const [isDeleting, setIsDeleting] = useState(false);
  const [text, setText] = useState("");
  const [delta, setDelta] = useState(300 - Math.random() * 100);
  const [index, setIndex] = useState(1);
  const toRotate = [
    "Business > Job",
    "Being Rich > Being Poor",
    "Escape the Rat-Race",
    "Make Money",
    "There is no nobility in Poverty"
  ];
  const period = 2000;

  useEffect(() => {
    let ticker = setInterval(() => {
      tick();
    }, delta);

    return () => {
      clearInterval(ticker);
    };
  }, [text]);

  const tick = () => {
    let i = loopNum % toRotate.length;
    let fullText = toRotate[i];
    let updatedText = isDeleting
      ? fullText.substring(0, text.length - 1)
      : fullText.substring(0, text.length + 1);

    setText(updatedText);

    if (isDeleting) {
      setDelta((prevDelta) => prevDelta / 2);
    }

    if (!isDeleting && updatedText === fullText) {
      setIsDeleting(true);
      setIndex((prevIndex) => prevIndex - 1);
      setDelta(period);
    } else if (isDeleting && updatedText === "") {
      setIsDeleting(false);
      setLoopNum(loopNum + 1);
      setIndex(1);
      setDelta(500);
    } else {
      setIndex((prevIndex) => prevIndex + 1);
    }
  };

  return (
    <section className="banner" id="home">
      <Helmet>
        <title>ConquerortheCrown</title>
        <meta
          name="description"
          content="This is the official website of ConquerortheCrown"
        />
        <meta
          name="keywords"
          content=" what is conquerortherown,conquerorthecrown download, ctc, Conqueror the crown, Escape the ratrace, Personal andrew tate, andrew tate genai, mentor to become rich, conquerorthecrown webapp, andrew tate chatbot"
        />
        <meta property="og:type" content="website" />
        <meta property="og:url" content="https://www.conquerorthecrown.com/" />
        <meta property="og:title" content="conquerorthecrown" />
        <meta
          property="og:description"
          content="This is the official website of ConquerortheCrown"
        />
        <meta
          property="og:image"
          content="https://miro.medium.com/v2/resize:fit:384/format:webp/1*IXsZwhAiDWCP7efpE-c0KQ.png"
        />
      </Helmet>
      <Container>
        <Row className="aligh-items-center">
          <Col >
            <TrackVisibility>
              {({ isVisible }) => (
                <div
                  className={
                    isVisible ? "animate__animated animate__fadeIn" : ""
                  }
                >
                  <h2 style={{ marginTop: 40, fontSize: 36, display: 'flex', alignItems: 'center',justifyContent: 'center'}}>
                    <span
                      className="txt-rotate"
                      dataPeriod="1000"
                      data-rotate='[ "Business > Job","Being Rich > Being Poor", "Escape the Rat-Race", "Make Money", "There is no nobility in Poverty"]'
                    >
                      <span className="wrap">{text}</span>
                    </span>
                  </h2>

                  <h1>Welcome to Conquerorthecrown</h1>
                  <span className="tagline">
                    {" "}
                    the best decision of your life{" "}
                  </span>
                  <button
                    onClick={() => {
                      window.location.href =
                        "https://app.conquerorthecrown.com/";
                    }}
                  >
                    {"Try ConquerortheCrown "}
                    <ArrowRightCircle size={25} />
                  </button>
                  {/* <a href="https://drive.google.com/drive/folders/1MD6NW_s1RkPtAy-jRL_MDd8lxIwkqJx-?usp=drive_link">
                    <button className="vvd">
                      <span>Download Now</span>
                    </button>
                  </a> */}
                </div>
              )}
            </TrackVisibility>
          </Col>
          
        </Row>
      </Container>
    </section>
  );
};
